<template>
  <div class="article-edit-container">
    <div class="article-edit-bar">
      <span class="article-edit-bar-title">{{
        article.title === "" ? "" : article.title
      }}</span>
      <span class="article-edit-bar-operator">
        <span class="article-edit-bar-operator-item">
          <t-button theme="warning" variant="dashed" @click="openDrawer"
            ></t-button
          >
        </span>
        <span class="article-edit-bar-operator-item">
          <t-button theme="danger" variant="base" @click="saveDraft"
            ></t-button
          >
        </span>
        <span class="article-edit-bar-operator-item">
          <t-button theme="primary" variant="base" @click="preview"
            ></t-button
          >
        </span>
        <span class="article-edit-bar-operator-item">
          <t-button theme="primary" variant="base" @click="handlePublish"
            ></t-button
          >
        </span>
      </span>
    </div>
    <div class="article-edit-title">
      <t-input size="large" placeholder="" v-model="article.title" />
    </div>
    <div class="vditor-container">
      <Vditor
        :originalContent="article.originalContent"
        @contentChange="onContentChange"
        @countWord="countWord"
      ></Vditor>
    </div>
    <t-tabs defaultValue="basic">
      <t-tab-panel value="basic">
        <template #label>
          <icon name="setting" style="margin-right: 4px" /> 
        </template>
        <div class="basic-setting-form-container">
          <t-form ref="form" labelAlign="top" :colon="true">
            <t-form-item
              label=""
              name="slug"
              :help="
                'http://alkaidchen.com/article/' +
                (article.slug === '' ? '{slug}' : article.slug)
              "
            >
              <t-input
                v-model="article.slug"
                placeholder="id"
              ></t-input>
            </t-form-item>
            <t-form-item label="" name="allowedComment">
              <t-switch v-model="article.allowedComment"></t-switch>
            </t-form-item>
            <t-form-item label="" name="isTop">
              <t-switch v-model="article.isTop"></t-switch>
            </t-form-item>
            <t-divider></t-divider>
            <t-form-item label="" name="category">
              <div class="category-container">
                <t-checkbox-group
                  v-model="article.categories"
                  :options="categoryOptions"
                ></t-checkbox-group>
                <div class="create-category-container">
                  <div class="create-category-button-container">
                    <t-button
                      theme="primary"
                      @click="showAddCategoryForm = true"
                      v-show="!showAddCategoryForm"
                    >
                      <add-icon slot="icon" size="200px" />
                      
                    </t-button>
                  </div>
                  <div class="create-category-form-container">
                    <t-form
                      :data="newCategory"
                      ref="form"
                      v-show="showAddCategoryForm"
                      scrollToFirstError="smooth"
                      labelWidth="0"
                    >
                      <div class="create-category-form-item">
                        <t-form-item name="name">
                          <t-input
                            v-model="newCategory.name"
                            placeholder=""
                          ></t-input>
                        </t-form-item>
                      </div>
                      <div class="create-category-form-item">
                        <t-form-item
                          name="slug"
                          :help="
                            'http://alkaidchen.com/category/' +
                            (newCategory.slug === ''
                              ? '{slug}'
                              : newCategory.slug)
                          "
                        >
                          <t-input
                            v-model="newCategory.slug"
                            placeholder=""
                          ></t-input>
                        </t-form-item>
                      </div>
                      <div class="create-category-form-item">
                        <t-form-item>
                          <t-button
                            theme="primary"
                            style="margin-right: 10px"
                            @click="handleCreateCategory"
                            ></t-button
                          >
                          <t-button
                            theme="default"
                            variant="base"
                            style="margin-right: 10px"
                            @click="cancelCreateCategory"
                            ></t-button
                          >
                        </t-form-item>
                      </div>
                    </t-form>
                  </div>
                </div>
              </div>
            </t-form-item>
            <t-divider></t-divider>
            <t-form-item label="" name="tags">
              <t-select
                v-model="article.tags"
                creatable
                filterable
                multiple
                :minCollapsedNum="3"
                :options="tagOptions"
                @create="handleCreateTag"
              />
            </t-form-item>
            <t-form-item label="" name="summary">
              <t-textarea
