                placeholder=""
                name="summary"
                :autosize="{ minRows: 6 }"
              />
            </t-form-item>
            <t-form-item label="" name="cover">
              <div class="article-cover-setting">
                <div class="article-cover-preview">
                  <img
                    :src="
                      article.cover === ''
                        ? 'http://119.91.26.252:8090/images/placeholder.jpg'
                        : article.cover
                    "
                  />
                </div>
                <div class="article-cover-input">
                  <t-input
                    clearable
                    v-model="article.cover"
                    placeholder=""
                  ></t-input>
                </div>
              </div>
            </t-form-item>
          </t-form>
        </div>
      </t-tab-panel>
      <t-tab-panel value="advanced">
        <template #label>
          <icon name="internet" style="margin-right: 4px" /> 
        </template>
        <div class="advanced-setting-form-container">
          <t-form ref="form" labelAlign="top" :colon="true">
            <t-form-item label="" name="password">
              <t-input
                type="password"
                v-model="article.password"
                placeholder=""
              >
              </t-input>
            </t-form-item>
            <t-form-item label="SEO " name="keywords">
              <t-select
                v-model="article.seoKeywords"
                creatable
                filterable
                multiple
                placeholder=""
                :options="seoKeywordOptions"
                :minCollapsedNum="3"
                @create="createSEOKeyword"
              />
            </t-form-item>
            <t-form-item label="SEO " name="description">
              <t-textarea
                v-model="article.seoDescription"
                placeholder=""
                name="seoDescription"
                :autosize="{ minRows: 6 }"
              />
            </t-form-item>
          </t-form>
        </div>
      </t-tab-panel>
    </t-tabs>
    <t-drawer
      header=""
      :visible="attachDrawerVisible"
      size="480px"
      @cancel="attachDrawerVisible = false"
      :onOverlayClick="() => (attachDrawerVisible = false)"
      :onConfirm="() => (attachDrawerVisible = false)"
      placement="right"
    >
      <p></p>
    </t-drawer>
  </div>
</template>

<script>
import Vditor from "@/components/vditor/Vditor.vue";
import { Icon, AddIcon } from "tdesign-icons-vue";

export default {
  name: "ArticleEdit",
  data() {
    return {
      attachDrawerVisible: false,
      article: {
        title: "",
        originalContent: "",
        formatContent: "",
        slug: "",
        allowedComment: true,
        isTop: false,
        categories: [],
        tags: [],
        summary: "",
        cover: "",
        password: "",
        wordCount: 0,
        seoKeywords: [],
        seoDescription: "",
        status: 0,
      },
      categoryOptions: [],
      tagOptions: [],
      seoKeywordOptions: [],
      showAddCategoryForm: false,
      newCategory: {
        name: "",
        slug: "",
      },
    };
  },
  methods: {
    // get content from sub component Vditor
    onContentChange(originalContent, formatContent) {
      this.article.originalContent = originalContent;
      this.article.formatContent = formatContent;
    },
    // get word count from sub component Vditor
    countWord(length) {
      this.article.wordCount = length;
    },
    saveDraft() {
      console.log("");
    },
    preview() {
      console.log("");
    },
    openDrawer() {
      this.attachDrawerVisible = true;
    },
    handlePublish() {
      //validate
      if (this.article.title === "") {
        this.$message.warning("");
        return;
      }
      this.$request
        .post("article", this.article)
        .then((res) => {
          if (res.status === 0) {
            this.$message.success("");
            this.$router.push({ name: "ArticleList" });
          }
        })
        .catch(() => {
          this.$message.warning("");
          // TODO: 
        });
    },
    handleCreateCategory() {
      // remove whitespace
      this.newCategory.name = this.newCategory.name.replace(/\s*/g, "");
      this.newCategory.slug = this.newCategory.slug.replace(/\s*/g, "");
      if (this.newCategory.name === "") {
        this.$message.warning("");
        return;
      }
      if (this.newCategory.slug === "") {
        this.$message.warning("");
        return;
      }
      this.$request
        .post("category", this.newCategory)
        .then((res) => {
          if (res.status === 0) {
            this.$message.success("");
            this.categoryOptions.push({
              value: res.data.id,
              label: res.data.name,
            });
            this.cancelCreateCategory();
          }
        })
        .catch(() => {
          this.$message.warning("");
          // TODO: 
        });
    },
    cancelCreateCategory() {
      this.newCategory.name = "";
      this.newCategory.slug = "";
      this.showAddCategoryForm = false;
    },
    handleCreateTag(value) {
      value = value.replace(/\s*/g, "");
      if (value === "") {
        this.$message.warning("");
        return;
      }
      var newTag = {
        name: value,
        slug: value,
      };
      this.$request
        .post("tag", newTag)
        .then((res) => {
          this.$message.success("");
          this.tagOptions.push({
            value: res.data.id,
            label: res.data.name,
          });
          this.article.tags.splice(this.article.tags.indexOf(value), 1);
          this.article.tags.push(res.data.id);
        })
        .catch(() => {
          this.article.tags.splice(this.article.tags.indexOf(value), 1);
          this.$message.error("");
          // TODO: 
        });
    },
    createSEOKeyword(value) {
      this.seoKeywordOptions.push({
        value,
        label: value,
      });
    },
  },
  components: { Vditor, Icon, AddIcon },
  mounted() {
    // list categories
    this.$request.get("categories").then((res) => {
      res.data.forEach((category) => {
        this.categoryOptions.push({
          value: category.id,
          label: category.name,
        });
      });
    });

    // list tags
    this.$request.get("tags").then((res) => {
      res.data.forEach((tag) => {
        this.tagOptions.push({
          value: tag.id,
          label: tag.name,
        });
      });
    });
  },
};
</script>

