<template>
  <div id="vditor"></div>
</template>

<script>
import Vditor from "vditor";
import "vditor/dist/index.css";

export default {
  data() {
    return {
      contentEditor: null,
    };
  },
  props: {
    origionContent: {
      type: String,
      required: false,
      default: "",
    },
  },
  methods: {
    contentChange() {
      this.$emit(
        "contentChange",
        this.contentEditor.getValue(),
        this.contentEditor.getHTML()
      );
    },
    countWord(length) {
      this.$emit(
        "countWord",
        length
      )
    }
  },
  mounted() {
    this.contentEditor = new Vditor("vditor", {
      height: 800,
      icon: "material",
      input: this.contentChange,
      counter: {
        enable: true,
        type: "text",
        after: (length) => {
          this.countWord(length)
        }
      },
      preview: {
        delay: 50,
      },
      outline: {
        enable: true,
      },
      cache: {
        enable: false,
      },
      after: () => {
        if (this.$store.state.app.isDark) {
          this.contentEditor.setTheme("dark", "dark");
        } else {
          this.contentEditor.setTheme("classic", "light");
        }
        this.contentEditor.insertValue(this.origionContent);
      },
    });
  },
  watch: {
    //  store
    "$store.state.app.isDark": {
      deep: true,
      handler: function (newValue) {
        // resulttrue
        if (newValue) {
          this.contentEditor.setTheme("dark", "dark");
        } else {
          this.contentEditor.setTheme("classic", "light");
        }
      },
    },
  },
};
</script>

